import Vue from 'vue'
import Vuex from 'vuex'
import { menuRouter, routes } from '../router/router'
Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    menuList: [...menuRouter],
    hearderName: ''
  },
  mutations: {
    getRouterName (state, payload) {
      let arr = []
      routes.forEach(item => {
        item.children.forEach(ele => {
          arr.push(ele)
        })
      })
      let index = arr.filter(item => item.name === payload)
      state.hearderName = index[0].title
    }
  },
  actions: {

  },
  getters: {
    getRouterName (state) {
      return state.hearderName
    }
  }
})
